import React, { useState } from 'react';
import { withStyles } from '@material-ui/core';
import style from './style';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import * as firebase from 'firebase';
import 'firebase/firestore';
const firebaseConfig = {
  apiKey: "AIzaSyAD2nlqnNZfUtGaV_Z6Ta_eB55N27-6jrM",
  authDomain: "classroom-emails.firebaseapp.com",
  databaseURL: "https://classroom-emails.firebaseio.com",
  projectId: "classroom-emails",
  storageBucket: "classroom-emails.appspot.com",
  messagingSenderId: "932392480727",
  appId: "1:932392480727:web:2c5303bf16a92a6a85a592",
  measurementId: "G-ZERZV8Q07P"
};
firebase.initializeApp(firebaseConfig);
const db = firebase.firestore();

const Feedback = ({
  success,
  inputedName,
  inputedDate,
  generatedEmail,
  classes
}) => {
  return (
    <div className={classes.main}>
      {
        success
        ? <SuccessFeedback />
        : <FailedFeedback
          classes={classes}
          name={inputedName}
          date={inputedDate}
          studentEmail={generatedEmail}
        />
      }
    </div>
  );
}

const SuccessFeedback = () => {
  return <div>
    Ficamos felizes que tenha coseguido! Boas aulas!
  </div>
};

const FailedFeedback = ({ classes, name, date, studentEmail }) => {
  const [contactEmail, setContactEmail] = useState('');
  const [contactPhone, setContactPhone] = useState('');

  return <div>
    <p>
      Caso tenha logado, mas não apareceu a turma do aluno, certifique-se que fez o acesso com a conta correta (não irá aparecer caso esteja com seu email pessoal, o acesso deve ser feito com o email e senha  acima)
    </p>
    <p>
      Se mesmo assim, não deu certo. Por favor, nos deixe seu email e telefone para contato. A equipe de suporte da EMEI Aristides entrará em contato com você!
    </p>
    <div className={classes.feedbackForm}>
      <div className={classes.formControl}>
        <TextField
          label="Email para contato"
          variant="outlined"
          value={contactEmail}
          className={classes.formField}
          onChange={(e) => {
            setContactEmail(e.target.value)
          }}
          />
      </div>
      <div className={classes.formControl}>
        <TextField
          label="Telefone para contato"
          variant="outlined"
          value={contactPhone}
          className={classes.formField}
          onChange={(e) => {
            setContactPhone(e.target.value)
          }}
        />
      </div>
      <div>
        <Button
          variant='contained'
          color='primary'
          onClick={() => {
            alert('ok')
            db.collection("contacts").add({
              name, date, studentEmail, contactEmail, contactPhone
            })
          }}
        >
          Solicitar contato
        </Button>
      </div>
    </div>
  </div>
};

export default withStyles(style)(Feedback);
